import Style from './burger-ingredients.module.css';
import TabMenu from '../tabs/tabs';
import { useSelector } from "react-redux";
import IngredientCategory from '../ingredient-category/ingredient-category';
import { useRef, useState, useMemo } from 'react';
import { getAllIngredientsData } from '../../components/utils/utils';

export default function BurgerIngredients() {
  //       
  const data = useSelector(getAllIngredientsData);
  const bunsArr = useMemo(
    () => { return data.filter((bun) => bun.type === "bun"); }, [data]);
  const sauceArr = useMemo(
    () => { return data.filter((sauce) => sauce.type === "sauce"); }, [data]);
  const mainArr = useMemo(
    () => { return data.filter((main) => main.type === "main"); }, [data]);

  //          
  const [current, setCurrent] = useState('bun');
  const containerRef = useRef(null);
  const bunRef = useRef(null);
  const sauceRef = useRef(null);
  const mainRef = useRef(null);

  //     ,        
  //    ,      
  const handleScroll = () => {
    const containerRect = containerRef.current.getBoundingClientRect();
    const bunRect = bunRef.current.getBoundingClientRect();
    const sauceRect = sauceRef.current.getBoundingClientRect();
    const mainRect = mainRef.current.getBoundingClientRect();
    if (containerRect.top >= bunRect.top) {
      setCurrent('bun');
    }
    if (containerRect.top >= sauceRect.top) {
      setCurrent('sauce');
    }
    if (containerRect.top >= mainRect.top) {
      setCurrent('main');
    }
  };

  return (
    <section className={`${Style.section} pt5 mb-10`}>
      <h1 className="text text_type_main-large pt-10 pb-5"> </h1>
      <TabMenu current={current} />
      <ul className={`${Style.listingridients}`} ref={containerRef} onScroll={handleScroll}>
        <IngredientCategory data={bunsArr} header='' id="bun" ref={bunRef} />
        <IngredientCategory data={sauceArr} header='' id="sauce" ref={sauceRef} />
        <IngredientCategory data={mainArr} header='' id="main" ref={mainRef} />
      </ul>
    </section>
  )
}



