import { SERVER_URL } from "../utils/serverUrl";

class Api {
  constructor(server) {
    this._server = server;
  }

  /**
   *     
   */
  _getResponse(res) {
    return res.ok
      ? res.json()
      : Promise.reject(`-   : ${res.status}`);
  };

  //    -----------------------------------------------
  /**
   *    Api   
   */
  getIngredients() {
    return fetch(`${this._server}ingredients`)
      .then((res) => this._getResponse(res))
  };
  /**
   *           
   */
  getOrderNumber(data) {
    return fetch(`${this._server}orders`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        "ingredients": data,
      })
    })
      .then((res) => this._getResponse(res))
  };

  /**
   *    
   */
  registerUser(user) {
    return fetch(`${this._server}auth/register`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(user),
    })
      .then((res) => this._getResponse(res))
  };
  /**
   *    
   */
  loginUser(user) {
    return fetch(`${this._server}auth/login`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(user),
    })
      .then((res) => this._getResponse(res));
  }
  /**
   *    
   */
  refreshToken(data) {
    return fetch(`${this._server}auth/token`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        "token": data,
      }),
    })
      .then((res) => this._getResponse(res));
  }
  /**
   *     
   */
  checkUser(accessToken) {
    return fetch(`${this._server}auth/user`, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        authorization: accessToken,
      },
    })
      .then((res) => this._getResponse(res));
  }
  /**
   *      
   */
  logOut(refreshToken) {
    return fetch(`${this._server}auth/logout`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        "token": refreshToken,
      }),
    })
      .then((res) => this._getResponse(res));
  }
  /**
   *    
   */
  recoverPassword(inputEmail) {
    return fetch(`${this._server}password-reset`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        "email": inputEmail,
      }),
    }).then((res) => this._getResponse(res));
  }
  /**
   *       
   */
  resetPassword(inputPassword, inputCode) {
    return fetch(`${this._server}password-reset/reset`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        "password": inputPassword,
        "token": inputCode,
      }),
    }).then((res) => this._getResponse(res));
  }
  /**
   *     
   */
  editUserData(user, accessToken) {
    return fetch(`${this._server}auth/user`, {
      method: 'PATCH',
      headers: {
        "Content-Type": "application/json",
        authorization: accessToken,
      },
      body: JSON.stringify(user),
    }).then((res) => this._getResponse(res));
  }
};

export const api = new Api(SERVER_URL);
