import Style from "./forgot-password.module.css";
import {
  EmailInput,
  Button
} from '@ya.praktikum/react-developer-burger-ui-components';
import { useState } from "react";
import {
  useDispatch, 
  useSelector
} from "react-redux";
import {
  Link,
  Navigate
} from 'react-router-dom';
import { getSuccessUserData } from '../../components/utils/utils'; //    
import { passwordRecover } from "../../services/actions/user"; //action 

export default function PasswordForgotPage() {
  //   
  const [email, setEmail] = useState("");
  //   
  const onChange = e => {
    setEmail(e.target.value);
  };

  //        
  const success = useSelector(getSuccessUserData);

  const dispatch = useDispatch();
  //,       
  const handlerSubmit = (e) => {
    e.preventDefault();
    dispatch(passwordRecover(email));
  };

  if (success) {
    return <Navigate to="/reset-password" />;
  }

  return (
    <main className={Style.forgot}>
      <h2 className={`text text_type_main-medium mb-6`}> </h2>
      <form className={Style.form} onSubmit={handlerSubmit}>
        <EmailInput
          placeholder={' e-mail'}
          type="email"
          required
          value={email}
          onChange={onChange}
        />
        <Button htmlType="submit">
          
        </Button>
      </form>
      <p className={`${Style.nav} text text_type_main-default text_color_inactive`}>
         ?
        <Link to="/login" className={Style.link}></Link>
      </p>
    </main>
  );
};
