import Style from './login-page.module.css';

import {
  Button,
  EmailInput,
  PasswordInput,
} from "@ya.praktikum/react-developer-burger-ui-components";
import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  Link,
  Navigate
} from "react-router-dom";
import { loginUser } from '../../services/actions/user'; //action  
import { getSuccessUserAuth } from '../../components/utils/utils'; //    

export default function LoginPage() {
  //   
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  //   
  const onChangeEmail = e => {
    setEmail(e.target.value);
  };
  const onChangePassword = e => {
    setPassword(e.target.value);
  };

  //     
  const loginSuccess = useSelector(getSuccessUserAuth);

  const dispatch = useDispatch();
  //,       
  const handlerSubmit = (e) => {
    e.preventDefault();
    const user = {
      "email": email,
      "password": password,
    };
    dispatch(loginUser(user));
  };

  if (loginSuccess) {
    return <Navigate to="/" />;
  }

  return (
    <main className={Style.login}>
      <h2 className={`text text_type_main-medium mb-6`}></h2>
      <form className={Style.form} onSubmit={handlerSubmit}>
        <EmailInput
          placeholder={'E-mail'}
          type="email"
          required
          value={email}
          onChange={onChangeEmail}
        />
        <PasswordInput
          placeholder={''}
          icon={"ShowIcon"}
          type="password"
          required
          value={password}
          onChange={onChangePassword}
        />
        <Button htmlType="submit"></Button>
      </form>
      <ul className={Style.nav}>
        <li className={`${Style.txt} text text_type_main-default text_color_inactive`}>
           -  ? <Link to="/register" className={Style.link}></Link>
        </li>
        <li className={`${Style.txt} text text_type_main-default text_color_inactive`}>
           ? <Link to="/forgot-password" className={Style.link}> </Link>
        </li>
      </ul>
    </main>
  );
};
