import Style from './reset-password-page.module.css';
import {
  PasswordInput,
  Input,
  Button
} from '@ya.praktikum/react-developer-burger-ui-components';
import { useState } from 'react';
import {
  useDispatch,
  useSelector
} from "react-redux";
import {
  Link,
  Navigate
} from 'react-router-dom';
import { getSuccessResetPassword, getSuccessPassRecover } from '../../components/utils/utils'; //    
import { passwordReset } from "../../services/actions/user"; //action 

export default function PasswordResetPage() {
  //   
  const [password, setPassword] = useState("");
  const [code, setCode] = useState("");
  //   
  const onChangePassword = e => {
    setPassword(e.target.value);
  };
  const onChangeCode = e => {
    setCode(e.target.value);
  };

  //      
  const successReset = useSelector(getSuccessResetPassword);
  const successRecover = useSelector(getSuccessPassRecover);

  const dispatch = useDispatch();
  //,       
  const handlerSubmit = (e) => {
    e.preventDefault();
    dispatch(passwordReset(password, code));
  };

  if (successReset) {
    return <Navigate to="/login" />;
  }

  return (
    <main className={Style.reset} >
      { !successRecover && <Navigate to="/forgot-password" />}
      <h2 className={`text text_type_main-medium mb-6`}> </h2>
      <form className={Style.form} onSubmit={handlerSubmit}>
        <PasswordInput
          placeholder={"  "}
          type="password"
          required
          value={password}
          onChange={onChangePassword}
        />
        <Input
          placeholder={"   "}
          type="text"
          required
          value={code}
          onChange={onChangeCode}
        />
        <Button htmlType="submit"></Button>
      </form>
      <p className={`${Style.nav} text text_type_main-default text_color_inactive`}>
         ?
        <Link to="/login" className={Style.link}></Link>
      </p>
    </main>
  )
};
